
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
components: {
Flexbox,
FlexboxItem
},
data () {
return {
songmenu: []
}
},
mounted () {
this.$http({
method: 'GET',
url: './static/data/songmenu.json'
})
.then(response => {
this.songmenu = response.data
})
},
methods: {
updateTitle (name) {
this.$store.dispatch('updateTitle', name)
}
}
}



